
/*Hamburger-nav */
#bar_container button{
    position: fixed;
    right: 0;
    top: 0;
    height: 3.5rem;
    border: none;
    outline: none;
    background-color: grey;
    color: white;
    text-align: center;
    text-decoration: none;
    cursor: pointer;
    z-index: 30;
}
#bar1,
#bar2,
#bar3 {
    width: 1.5rem;
    height: 0.25rem;
    background-color: #FFF;
    margin: 0.25rem 0;
    transition: 0.4s;
}
.changed button{
  background-color: #111 !important;
}
.changed #bar1 {
    -webkit-transform: rotate(-45deg) translate(-0.35rem, 0.25rem);
    transform: rotate(-45deg) translate(-0.45rem, 0.25rem);
}
.changed #bar2 {
    opacity: 0;
}
.changed #bar3 {
    -webkit-transform: rotate(45deg) translate(-0.35rem, -0.25rem);
    transform: rotate(45deg) translate(-0.45rem, -0.25rem);
}
/*END Hamburger-nav */

/* The side navigation menu */
#mainCon {
    transition: margin-left .5s;
}
#mySidenav {
    position: fixed; 
    top: 0; 
    right: 0;
    height: 100%;
    width: 0;
    background-color: #111; 
    padding-top: 60px; 
    transition: 0.5s; 
    overflow-x: hidden;
    z-index: 30;
}

/* The navigation menu links */
#mySidenav button {
    padding: 8px 8px 8px 32px;
    text-decoration: none;
    font-size: 25px;
    color: #818181;
    display: block;
    transition: 0.3s;
    position: absolute;
    top: 0;
    right: 25px;
    font-size: 36px;
    margin-left: 50px;
}

#mySidenav button:hover {
    color: #f1f1f1;
}
#subMenu li{
  display: flex;
  display: -webkit-box;  
  display: -ms-flexbox;  
  display: -webkit-flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
